// ProductViewer.tsx
import React, { useState, useEffect } from "react";
import RotatingModel from "./RotatingModel";
import { PointOfInterest } from "@/interfaces/CoreInterfaces";
import ReactDOM from "react-dom/client";
import { Box, Button } from "@mui/material";

interface ProductViewerProps {
  modelPois: PointOfInterest[]; // Array of models
  canvasRef: React.RefObject<HTMLCanvasElement>; // Reference to canvas
  uiRef: React.RefObject<HTMLDivElement>; // Reference to UI container
}

const ProductViewer: React.FC<ProductViewerProps> = ({ modelPois, canvasRef, uiRef }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [transitioning, setTransitioning] = useState(false);
  const [direction, setDirection] = useState(1);
  const [xView, setXView] = useState(false); // Cross-section toggle

  // Flatten model arrays from all POIs
  const models = modelPois.map((poi) => poi.modelData).flat();
  const currentModel = models[currentIndex];

  const switchModel = (dir: "next" | "prev") => {
    if (transitioning || !models.length) return;

    setDirection(dir === "next" ? 1 : -1);
    setTransitioning(true);

    const newIndex = dir === "next" ? (currentIndex + 1) % models.length : (currentIndex - 1 + models.length) % models.length;

    setTimeout(() => {
      setCurrentIndex(newIndex);
      setTransitioning(false);
    }, 300); // Match animation duration
  };

  // Dynamically render the buttons overlay using a portal into the UI container.
  useEffect(() => {
    if (!uiRef.current) return;
    const uiContainer = uiRef.current;

    // Create a container for the buttons overlay
    const container = document.createElement("div");
    container.id = "product-viewer-buttons-container";
    uiContainer.appendChild(container);

    // Create a root and render the ProductViewerButtons into the container
    const root = ReactDOM.createRoot(container);
    root.render(<ProductViewerButtons onPrev={() => switchModel("prev")} onNext={() => switchModel("next")} />);

    return () => {
      root.unmount();
      container.remove();
    };
  }, [uiRef, currentIndex, transitioning]);

  return (
    <>
      {currentModel && (
        <RotatingModel
          modelData={currentModel}
          canvasRef={canvasRef}
          transitioning={transitioning}
          direction={direction}
          xView={xView}
          onClick={() => setXView((prev) => !prev)} // Toggle cross-section
        />
      )}
    </>
  );
};

interface ProductViewerButtonsProps {
  onPrev: () => void;
  onNext: () => void;
}

const ProductViewerButtons: React.FC<ProductViewerButtonsProps> = ({ onPrev, onNext }) => {
  return (
    <Box
      sx={{
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        pointerEvents: "none", // Container doesn't capture clicks; buttons inside will
      }}
    >
      <Button
        onClick={onPrev}
        sx={{
          position: "absolute",
          width: "10%",
          height: "16%",
          left: 0,
          top: "42.5%",
          transform: "translateY(-50%)",
          bgcolor: "grey.700",
          color: "white",
          p: 1,
          pointerEvents: "auto", // Enable clicks on this button
          opacity: 0, // Adjust as needed
        }}
      >
        
      </Button>
      <Button
        onClick={onNext}
        sx={{
          position: "absolute",
          width: "10%",
          height: "16%",
          right: "8%",
          top: "42.5%",
          transform: "translateY(-50%)",
          bgcolor: "grey.700",
          color: "white",
          p: 1,
          pointerEvents: "auto",
          opacity: 0,
        }}
      >
        
      </Button>
    </Box>
  );
};

export default ProductViewer;
