import React, { useEffect } from "react";
import { createRoot } from "react-dom/client";
import { PoiInfo } from "../../interfaces/CoreInterfaces";
import InfoWindow from "./InfoWindow";

interface DisplayUiProps {
  uiRef: React.RefObject<HTMLDivElement>;
  poiInfo: PoiInfo;
}

const DisplayUi: React.FC<DisplayUiProps> = ({ uiRef, poiInfo }) => {
  useEffect(() => {
    const currentUiRef = uiRef.current;

    // Ensure the UI container exists
    if (!currentUiRef) return;

    // Create a container specifically for the InfoWindow
    let infoWindowContainer = document.getElementById("info-window-container");
    if (!infoWindowContainer) {
      infoWindowContainer = document.createElement("div");
      infoWindowContainer.style.position = "absolute";
      infoWindowContainer.style.top = "1rem";
      infoWindowContainer.style.right = "1rem";
      infoWindowContainer.style.width = "33.3333%";
      infoWindowContainer.style.transform = "translate(0, 0)";
      infoWindowContainer.id = "info-window-container";
      currentUiRef.appendChild(infoWindowContainer);
    }

    let root: ReturnType<typeof createRoot> | null = null;

    // Render InfoWindow if poiInfo exists
    if (poiInfo) {
      const infoWindow = <InfoWindow title={poiInfo.title} description={poiInfo.description} image={poiInfo.image} />;
      root = createRoot(infoWindowContainer);
      root.render(infoWindow);
    }

    // Cleanup function to unmount InfoWindow on component unmount
    return () => {
      if (root) {
        root.unmount();
      }
    };
  }, [uiRef]);

  return null; // This component does not render anything directly
};

export default DisplayUi;
