import React from "react";
import * as THREE from "three";
import { useThree } from "@react-three/fiber";

// Custom imports
import { ProjectData, UiElement } from "@/interfaces/CoreInterfaces";
import PoiBackButton from "@/modules/poi/PoiBackButton";
import InteractionHandler from "@/modules/poi/InteractionHandler";
import CameraController from "@/modules/poi/PoiCameraController";
import { createPoiIcon } from "@/components/3d/CreateIcon";
import PoiContentLoader from "@/modules/poi/PoiContent";
import DisplayPoiUi from "@/modules/poi/DisplayPoiUi";
import LoadModels from "@/utils/LoadModels";

const PoiHandler: React.FC<{
  projectData: ProjectData;
  canvasRef: React.RefObject<HTMLCanvasElement>;
  uiRef: React.RefObject<HTMLDivElement>;
  overlayRef: React.RefObject<HTMLDivElement>;
}> = ({ projectData, canvasRef, uiRef, overlayRef }) => {
  const uiElement = { iconSize: 400, iconPath: "/projects/trox/images/TroxOx.png" } as UiElement;
  const { camera, size } = useThree();

  if (projectData.sceneTitle === "Trox Demo") {
    createPoiIcon(uiElement, uiRef, camera, size, undefined, undefined, new THREE.Vector3(0, 14, 0));
  }

  return (
    <>
      {projectData.sceneModels && <LoadModels modelData={projectData.sceneModels} />}
      <PoiBackButton parentPoi={projectData.parentPoi} />
      <CameraController debugSettings={projectData.debugSettings} />
      <PoiContentLoader canvasRef={canvasRef} uiRef={uiRef} overlayRef={overlayRef} />
      <InteractionHandler uiRef={uiRef} />
      <DisplayPoiUi uiRef={uiRef} />
    </>
  );
};

export default PoiHandler;
