import * as THREE from "three";
import React, { useEffect, useRef } from "react";
import { useGLTF } from "@react-three/drei";
import { ModelData } from "@/interfaces/CoreInterfaces";
import { scenePath } from "@/app/Routing";
import { AnimationMixer } from "three";
import XViewModel from "@/utils/xViewModel";

const Model: React.FC<{ modelData: ModelData; xView?: boolean; tag?: string }> = ({ modelData, xView, tag }) => {
  if (modelData.path === undefined) {
    console.error("Model path is undefined");
    return null;
  }
  if (xView === undefined || modelData.xView === undefined) {
    return <DefaultModel modelData={modelData} tag={tag} />;
  } else {
    return <XViewModel modelData={modelData} scale={1} xView={xView} tag={tag} />;
  }
};

export default Model;

const DefaultModel: React.FC<{ modelData: ModelData; tag?: string }> = ({ modelData, tag }) => {
  const modelPath = scenePath + modelData.path;
  const mixerRef = useRef<AnimationMixer | null>(null);
  const { animations } = useGLTF(modelPath);
  const model = useGLTF(modelPath);

  useEffect(() => {
    if (mixerRef.current) {
      const clock = new THREE.Clock();
      const animate = () => {
        requestAnimationFrame(animate);
        const delta = clock.getDelta();
        if (mixerRef.current) {
          mixerRef.current.update(delta);
        }
      };
      animate();
    }
  }, [animations]);

  // check if the model should have loop animation
  if (modelData.loopAnimation) {
    const loopClip = animations.find((clip) => clip.name === modelData.loopAnimation);
    if (loopClip) {
      mixerRef.current = new THREE.AnimationMixer(model.scene);
      const action = mixerRef.current.clipAction(loopClip);
      action.play();
    } else {
      console.warn(`Loop animation ${modelData.loopAnimation} not found`);
    }
  }

  // Attach animations and mixer to the scene for later use
  model.scene.traverse((child) => {
    if ((child as THREE.Mesh).isMesh) {
      child.animations = animations;
    }
  });

  if (tag) {
    model.scene.traverse((child) => {
      if ((child as THREE.Mesh).isMesh) {
        const mesh = child as THREE.Mesh;
        mesh.userData.tag = tag;
      }
    });
  }
  return (
    <mesh
      castShadow={modelData.castShadow ? modelData.castShadow : false}
      receiveShadow={modelData.receiveShadow ? modelData.receiveShadow : false}
      scale={[modelData.scale ?? 1, modelData.scale ?? 1, modelData.scale ?? 1]}
    >
      <primitive object={model.scene} />
    </mesh>
  );
};

