import * as THREE from "three";
import React, { useEffect, useState, useRef } from "react";
import { useThree } from "@react-three/fiber";
import { PointOfInterest, ScrollStep } from "../../../interfaces/CoreInterfaces";
import { useMoveCamera } from "@/functions/MoveCamera";
import DisplayUi from "@2dc/DisplayInfoWindow";

interface ScrollViewProps {
  uiRef: React.RefObject<HTMLDivElement>; // Reference to the UI container
  poiData: PointOfInterest; // The POI containing ScrollView data
}

const ScrollView: React.FC<ScrollViewProps> = ({ uiRef, poiData }) => {
  const { scene } = useThree();
  const { scrollData } = poiData;
  const [currentStepIndex, setCurrentStepIndex] = useState(0);
  const [isScrolling, setIsScrolling] = useState(false);
  const [currentPoiInfo, setCurrentPoiInfo] = useState<PointOfInterest["poiInfo"] | null>(null);
  const { startCameraAnimation } = useMoveCamera();
  const originalMaterials = useRef<Map<string, THREE.Material>>(new Map());

  const handleScroll = (direction: 1 | -1) => {
    if (!scrollData) {
      console.error("No scrollData found for ScrollView POI");
      return;
    }
    if (isScrolling) {
      return;
    }

    let newIndex = currentStepIndex + direction;

    // Loop or clamp index
    if (scrollData.loop) {
      newIndex = (newIndex + scrollData.steps.length) % scrollData.steps.length;
    } else {
      newIndex = Math.max(0, Math.min(newIndex, scrollData.steps.length - 1));
    }

    if (newIndex !== currentStepIndex) {
      setIsScrolling(true);
      const step = scrollData.steps[newIndex];
      executeScrollStep(step, () => {
        setCurrentStepIndex(newIndex);
        setIsScrolling(false);
      });
    }
  };

  const executeScrollStep = (step: ScrollStep, onComplete: () => void) => {
    console.log("Executing scroll step", step);

    const stepObject = scene.getObjectByName(step.stepName);
    if (!stepObject) {
      console.error(`Object with name ${step.stepName} not found`);
      setCurrentPoiInfo(null); // Clear UI if no valid step object
      return onComplete();
    }

    // Restore original materials before applying new ones
    originalMaterials.current.forEach((material, uuid) => {
      const object = scene.getObjectByProperty("uuid", uuid);
      if (object && object instanceof THREE.Mesh) {
        object.material = material;
      }
    });

    // Get the target position from the stepObject
    const targetPosition = new THREE.Vector3();
    stepObject.getWorldPosition(targetPosition);
    targetPosition.add(step.cameraTransform?.target || new THREE.Vector3(0, 0, 0));

    // Get the camera offset from the stepObject
    const cameraOffset = step.cameraTransform?.position
      ? new THREE.Vector3(step.cameraTransform.position.x, step.cameraTransform.position.y, step.cameraTransform.position.z)
      : new THREE.Vector3(1, 0, 0);

    // Apply the stepObject's rotation to the camera offset
    const rotatedOffset = cameraOffset.applyQuaternion(stepObject.quaternion);

    const cameraPosition = targetPosition.clone().add(rotatedOffset);

    // Trigger the camera movement using the hook
    startCameraAnimation({
      target: { x: targetPosition.x, y: targetPosition.y, z: targetPosition.z },
      position: { x: cameraPosition.x, y: cameraPosition.y, z: cameraPosition.z },
    });

    // Update UI data for the current step
    if (step.poiInfo) {
      setCurrentPoiInfo(step.poiInfo);
    } else {
      setCurrentPoiInfo(null); // Clear UI if no poiInfo is available
    }

    if (step.editMaterial) {
      stepObject.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          // Store the original material if not already stored
          if (!originalMaterials.current.has(child.uuid)) {
            originalMaterials.current.set(child.uuid, child.material.clone());
          }
          const newMaterial = child.material.clone();
          newMaterial.emissiveIntensity = 0.4;
          newMaterial.emissive.set(0xffffbb);
          child.material = newMaterial;
        }
      });
    }

    setTimeout(onComplete, step.delay || 500);
  };

  useEffect(() => {
    const handleWheel = (event: WheelEvent) => {
      const direction = event.deltaY > 0 ? 1 : -1;
      handleScroll(direction);
    };

    window.addEventListener("wheel", handleWheel);
    return () => window.removeEventListener("wheel", handleWheel);
  }, [currentStepIndex, isScrolling]);

  useEffect(() => {
    return () => {
      // Restore original materials when component unmounts
      originalMaterials.current.forEach((material, uuid) => {
        const object = scene.getObjectByProperty("uuid", uuid);
        if (object && object instanceof THREE.Mesh) {
          object.material = material;
        }
      });
    };
  }, []);

  return <>{currentPoiInfo && <DisplayUi uiRef={uiRef} poiInfo={currentPoiInfo} />}</>;
};

export default ScrollView;
