import * as THREE from "three";
import { UiElement } from "@/interfaces/CoreInterfaces";

export function createPoiIcon(
  uiElement: UiElement,
  uiRef: React.RefObject<HTMLDivElement>,
  camera: THREE.Camera,
  size: { width: number; height: number },
  target?: THREE.Object3D,
  onClick?: () => void, // New parameter
  targetOffset?: THREE.Vector3
) {
  if (!uiRef.current) {
    console.error("UI container (uiRef) is not available.");
    return null;
  }

  const iconWidth = uiElement?.iconSize ?? 22;

  if (!target) {
    target = new THREE.Object3D();
    if (targetOffset) {
      target.position.copy(targetOffset);
    }
  }
  // Create or find the icon element
  let iconElement = target.userData.iconElement;

  if (!iconElement) {
    iconElement = document.createElement("div");
    iconElement.className = "icon-element";
    iconElement.style.position = "absolute";
    iconElement.style.transition = "opacity 300ms";
    iconElement.style.opacity = "0.6";
    iconElement.addEventListener("mouseover", () => {
      iconElement.style.opacity = "1";
    });
    iconElement.addEventListener("mouseout", () => {
      iconElement.style.opacity = "0.6";
    });
    iconElement.style.cursor = "pointer";

    iconElement.style.width = `${iconWidth}px`;
    iconElement.style.height = `${iconWidth}px`;
    const iconURL = uiElement?.iconPath ?? "/images/icon.png";
    iconElement.style.backgroundImage = `url('${iconURL}')`;
    iconElement.style.backgroundSize = "cover";
    iconElement.style.pointerEvents = "auto"; // Allow interaction with the icon

    // Attach the click handler
    iconElement.addEventListener("click", onClick);

    uiRef.current.appendChild(iconElement);

    // Save the icon element for later updates
    target.userData.iconElement = iconElement;
  }

  if (uiElement.title) {
    // Create or find the title element
    let titleElement = target.userData.titleElement;
    if (!titleElement) {
      titleElement = document.createElement("div");
      titleElement.className = "title-element";
      titleElement.style.position = "absolute";
      titleElement.style.color = "white";
      titleElement.style.backgroundColor = "rgba(0, 0, 0, 0.5)";
      titleElement.style.padding = "0.5rem 1rem";
      titleElement.style.borderRadius = "0.25rem";
      titleElement.innerText = uiElement.title;
      uiRef.current.appendChild(titleElement);

      // Save the title element for later updates
      target.userData.titleElement = titleElement;
    }

    // Function to update title position
    function updateTitlePosition() {
      const vector = new THREE.Vector3();
      if (target) {
        target.getWorldPosition(vector); // Get the world position of the target object
      }
      vector.project(camera); // Project the position into 2D screen space

      const x = (vector.x * 0.5 + 0.5) * size.width;
      const y = (-vector.y * 0.5 + 0.5) * size.height;

      const xPos = x - iconWidth / 2;
      const yPos = y + iconWidth;

      titleElement.style.left = `${xPos}px`;
      titleElement.style.top = `${yPos}px`;

      // titleElement.style.transform = `translate(-50%, -50%) translate(${x}px, ${y + iconWidth}px)`;
    }

    // Add an animation frame to continuously update the title position
    function animateTitle() {
      updateTitlePosition();
      requestAnimationFrame(animateTitle);
    }

    animateTitle();
  }

  // Function to update icon position
  function updateIconPosition() {
    const vector = new THREE.Vector3();
    if (target) {
      target.getWorldPosition(vector); // Get the world position of the target object
    }
    // add the positionOffset to the vector if it exists
    if (uiElement.positionOffset) {
      vector.add(uiElement.positionOffset);
    }
    vector.project(camera); // Project the position into 2D screen space

    const x = (vector.x * 0.5 + 0.5) * size.width;
    const y = (-vector.y * 0.5 + 0.5) * size.height;

    const xPos = x - iconWidth / 2;
    const yPos = y - iconWidth / 2;

    iconElement.style.left = `${xPos}px`;
    iconElement.style.top = `${yPos}px`;

    // iconElement.style.transform = `translate(-50%, -50%) translate(${x}px, ${y}px)`;
  }

  // Add an animation frame to continuously update the icon position
  function animate() {
    updateIconPosition();
    requestAnimationFrame(animate);
  }

  animate();

  // Clean up when the target or POI is removed
  return () => {
    if (iconElement && uiRef.current) {
      iconElement.removeEventListener("click", onClick);
      uiRef.current.removeChild(iconElement);
    }
    target.userData.iconElement = null;
  };
}
