import React, { useEffect } from "react";
import { VideoData } from "../../../interfaces/CoreInterfaces";
import { scenePath } from "@/app/Routing";

interface VideoFrameProps {
  videoData: VideoData;
  uiRef: React.RefObject<HTMLDivElement>;
  onClose: () => void; // Callback to close the video
}

const VideoFrame: React.FC<VideoFrameProps> = ({ videoData, uiRef, onClose }) => {
  useEffect(() => {
    if (!videoData || !videoData.videoUrl || !uiRef.current) {
      console.error("Invalid videoData or uiRef!");
      return;
    }

    const videoUrl = `${scenePath}/videos/${videoData.videoUrl}`;

    // Create video container with inline styles replacing Tailwind classes
    const videoContainer = document.createElement("div");
    videoContainer.style.position = "absolute";
    videoContainer.style.top = "0";
    videoContainer.style.left = "0";
    videoContainer.style.width = "100%";
    videoContainer.style.height = "100%";
    videoContainer.style.backgroundColor = "black";
    videoContainer.style.display = "flex";
    videoContainer.style.justifyContent = "center";
    videoContainer.style.alignItems = "center";
    videoContainer.style.pointerEvents = "auto";
    videoContainer.style.opacity = "0";
    videoContainer.style.zIndex = "1000";

    // Set transition style for fade-in/out
    const transitionStyle = videoData.fadeDuration ? `opacity ${videoData.fadeDuration / 1000}s ease-in-out` : "opacity 0.5s ease-in-out";
    videoContainer.style.transition = transitionStyle;

    // Create video element with inline styles replacing Tailwind's max-w-full and max-h-full
    const videoElement = document.createElement("video");
    videoElement.src = videoUrl;
    videoElement.controls = true;
    videoElement.autoplay = true;
    videoElement.loop = videoData.loop ?? false;
    videoElement.style.maxWidth = "100%";
    videoElement.style.maxHeight = "100%";
    videoContainer.appendChild(videoElement);

    // Create close button with inline styles replacing Tailwind classes
    const closeButton = document.createElement("button");
    closeButton.innerText = "X";
    closeButton.style.position = "absolute";
    closeButton.style.top = "0.5rem";
    closeButton.style.left = "0.5rem";
    closeButton.style.backgroundColor = "#374151";
    closeButton.style.color = "white";
    closeButton.style.fontSize = "1.125rem";
    closeButton.style.borderRadius = "0.25rem";
    closeButton.style.padding = "0.5rem 1rem";
    closeButton.style.opacity = "0.8";
    closeButton.style.pointerEvents = "auto";
    closeButton.style.zIndex = "1001";

    // Add hover effect: on mouseover, set opacity to 1; on mouseout, revert to 0.8
    closeButton.addEventListener("mouseover", () => {
      closeButton.style.opacity = "1";
    });
    closeButton.addEventListener("mouseout", () => {
      closeButton.style.opacity = "0.8";
    });

    // Set the click handler
    closeButton.onclick = () => {
      if (videoData.fadeOut !== false) {
        videoContainer.style.opacity = "0";
        setTimeout(() => onClose(), (videoData.fadeDuration ?? 500) + 10);
      } else {
        onClose();
      }
    };
    videoContainer.appendChild(closeButton);

    // Handle video end event for exitAfterEnd functionality
    videoElement.onended = () => {
      if (videoData.exitAfterEnd !== false) {
        if (videoData.fadeOut !== false) {
          videoContainer.style.opacity = "0";
          setTimeout(() => onClose(), (videoData.fadeDuration ?? 500) + 10);
        } else {
          onClose();
        }
      }
    };

    // When the video can play, apply fade-in effect
    videoElement.addEventListener("canplay", () => {
      if (videoData.fadeIn !== false) {
        setTimeout(() => {
          videoContainer.style.opacity = "1";
        }, 1000);
      } else {
        videoContainer.style.opacity = "1";
      }
    });

    // Append the video container to the provided UI container
    uiRef.current.appendChild(videoContainer);

    // Cleanup: remove the video container when the component unmounts
    return () => {
      if (uiRef.current && uiRef.current.contains(videoContainer)) {
        uiRef.current.removeChild(videoContainer);
      }
    };
  }, [videoData, uiRef, onClose]);

  return null; // This component doesn't render any DOM itself.
};

export default VideoFrame;
