import React from "react";
import { createRoot } from "react-dom/client";
import { UiElement } from "@/interfaces/CoreInterfaces";
import Button from "@/components/2d/CustomButton";

function createButton(uiElement: UiElement, uiRef: React.RefObject<HTMLDivElement>, onClick?: () => void) {
  if (!uiElement || !uiRef.current) {
    console.error("Invalid UI element or UI container.");
    return;
  }
  const buttonElement = <Button onClick={onClick}>{uiElement.name || "Button"}</Button>;
  if (!buttonElement) {
    console.error("Button element not created.");
    return;
  }
  let container = document.getElementById("poi-functionality-ui");
  if (!container) {
    container = document.createElement("div");
    container.id = "poi-functionality-ui";
    container.style.position = "absolute";
    container.style.display = "flex";
    container.style.bottom = "0";
    container.style.right = "0";
    container.style.padding = "1rem";
    uiRef.current.appendChild(container);
  }
  const root = createRoot(container);
  root.render(buttonElement);

  // Clean up when the target or POI is removed
  return () => {
    if (container) {
      container.remove();
    }
  };
}

export default createButton;
