// MoveCamera.ts
import * as THREE from "three";
import { useState } from "react";
import { useSpring } from "@react-spring/three";
import { useThree, useFrame } from "@react-three/fiber";

interface MoveCameraProps {
  target: { x: number; y: number; z: number };
  position: { x: number; y: number; z: number };
}

export const useMoveCamera = () => {
  const { camera, scene } = useThree();
  const [animate, setAnimate] = useState(false);

  const cameraFocusObject: THREE.Object3D | null = scene.getObjectByName("cameraFocusObject") as THREE.Object3D;
  if (!cameraFocusObject) {
    console.error("Camera focus object not found");
  }

  const [spring, api] = useSpring(() => ({
    cameraPosition: [0, 0, 0] as THREE.Vector3Tuple,
    cameraTarget: [0, 0, 0] as THREE.Vector3Tuple,
    config: { mass: 5, tension: 100, friction: 60, precision: 0.001, clamp: true },
    onStart: () => setAnimate(true),
    onRest: () => setAnimate(false),
  }));

  const startCameraAnimation = ({ target, position }: MoveCameraProps) => {
    api.start({
      from: {
        cameraPosition: camera.position.toArray() as THREE.Vector3Tuple,
        cameraTarget: cameraFocusObject?.position.toArray() as THREE.Vector3Tuple,
      },
      to: {
        cameraPosition: [position.x, position.y, position.z] as THREE.Vector3Tuple,
        cameraTarget: [target.x, target.y, target.z] as THREE.Vector3Tuple,
      },
    });
  };

  useFrame(() => {
    if (animate) {
      camera.position.set(...spring.cameraPosition.get());
      if (cameraFocusObject) {
        cameraFocusObject.position.set(...spring.cameraTarget.get());
        camera.lookAt(cameraFocusObject.position);
      }
    }
  });

  return { startCameraAnimation };
};
