import * as THREE from "three";
import React, { useEffect, useRef } from "react";
import { createRoot } from "react-dom/client";
import { Environment } from "@react-three/drei";
import { Canvas, useThree } from "@react-three/fiber";
import { usePoi } from "@/context/PoiContext";
import GlobalLights from "@3dc/GlobalLights";
import ProductViewer from "./poiTypes/ProductViewer";
import VideoFrame from "./poiTypes/VideoFrame";
import InspectObject from "./poiTypes/InspectObject";
import ScrollView from "./poiTypes/ScrollView";

const PoiContent: React.FC<{
  canvasRef: React.RefObject<HTMLCanvasElement>;
  uiRef: React.RefObject<HTMLDivElement>;
  overlayRef: React.RefObject<HTMLDivElement>;
}> = ({ canvasRef, uiRef, overlayRef }) => {
  const { activePoi, setActivePoi, previousPoi } = usePoi();
  const { scene } = useThree();

  const overLayCanvasRef = useRef<HTMLCanvasElement>(null);

  // TODO: Replace with a Canvas component that is not fetched through a useEffect hook
  useEffect(() => {
    if (!activePoi) return;

    if (activePoi.type === "productViewer" && activePoi.useOverlay && overlayRef.current) {
      // Clear previous overlay content
      overlayRef.current.innerHTML = "";

      // Render the ProductViewer directly into the overlayRef
      createRoot(overlayRef.current).render(
        <div
          style={{
            position: "fixed",
            top: "0",
            left: "0",
            width: "100%",
            height: "100%",
            backgroundColor: "rgba(64, 64, 64, 0.8)",
            backdropFilter: "blur(10px)",
          }}
        >
          <Canvas ref={overLayCanvasRef} camera={{ position: [0, 8, 20], fov: 60, near: 1, far: 40 }} shadows={true} className="pointer-events-auto">
            <GlobalLights preset="showcaseWarm" />
            <Environment preset="studio" environmentIntensity={0.2} />
            <ProductViewer modelPois={activePoi.childPois || []} canvasRef={overLayCanvasRef} uiRef={uiRef} />
          </Canvas>
        </div>
      );

      // Cleanup the overlay when activePoi changes or component unmounts
      return () => {
        if (overlayRef.current) {
          createRoot(overlayRef.current).unmount();
        }
      };
    } else {
      // Remove overlay content if not in `productViewer` POI
      if (overlayRef.current) {
        overlayRef.current.innerHTML = "";
      }
    }
  }, [activePoi, overlayRef, canvasRef, uiRef]);

  useEffect(() => {
    // Cleanup UI buttons in the main `uiRef` when exiting the ProductViewer POI
    if (activePoi?.type !== "productViewer" && uiRef.current) {
      const productViewerButtons = uiRef.current.querySelector<HTMLDivElement>("[data-product-viewer-buttons]");
      if (productViewerButtons) productViewerButtons.remove();
    }
  }, [activePoi, uiRef]);

  if (!activePoi) {
    console.error("No active POI found.");
    return null;
  }

  switch (activePoi.type) {
    case "productViewer":
      if (!activePoi.useOverlay) {
        if (!activePoi.childPois) {
          return null;
        }
        if (activePoi.meshId) {
          const poiObject = scene.getObjectByName(activePoi.meshId);
          if (poiObject) {
            const poiPosition = new THREE.Vector3();
            poiObject.getWorldPosition(poiPosition);
            return (
              <group position={poiPosition}>
                <ProductViewer modelPois={activePoi.childPois || []} canvasRef={canvasRef} uiRef={uiRef} />
              </group>
            );
          }
        }
        return <ProductViewer modelPois={activePoi.childPois || []} canvasRef={canvasRef} uiRef={uiRef} />;
      }
      return null; // If `useOverlay` is true, we don't return anything to render in the main scene.

    case "videoFrame":
      if (activePoi.videoData && previousPoi) {
        return <VideoFrame videoData={activePoi.videoData} uiRef={uiRef} onClose={() => setActivePoi(previousPoi)} />;
      } else {
        console.error("No video data found for videoFrame POI");
        return null;
      }

    case "inspectObject":
      if (activePoi.meshId && activePoi.inspectObjectData) {
        const { elevation, rotationSpeed } = activePoi.inspectObjectData;
        console.log("InspectObject", activePoi.meshId, elevation, rotationSpeed);
        return <InspectObject canvasRef={canvasRef} meshId={activePoi.meshId} elevation={elevation} rotationSpeed={rotationSpeed} />;
      } else {
        console.error("No meshId or inspectObjectData found for InspectObject POI");
        return null;
      }

    case "scrollView":
      if (activePoi.scrollData) {
        return <ScrollView uiRef={uiRef} poiData={activePoi} />;
      } else {
        console.error("No scrollData found for ScrollView POI");
        return null;
      }

    case "staticObject":
      return null;

    default:
      console.warn(`Unknown POI type: ${activePoi.type}`);
      return null;
  }
};

export default PoiContent;
