import React from "react";
import { ShowcaseLighting as ShowcaseLightingType, LightProps } from "@/interfaces/CoreInterfaces";

const GlobalLights: React.FC<ShowcaseLightingType> = ({ preset, lights }) => {
  const presets: Record<string, LightProps[]> = {
    showcaseWarm: [
      { type: "directional", position: { x: 5, y: 5, z: 5 }, intensity: 5, color: "#FFB6C1", castShadow: true, on: true },
      { type: "directional", position: { x: -5, y: 3, z: 5 }, intensity: 1, color: "#FFB6C1", on: true },
      { type: "directional", position: { x: 0, y: 5, z: -5 }, intensity: 2, color: "#FFB6C1", on: true },
      { type: "ambient", intensity: 0.3, color: "#FFB6C1", on: true },
    ],
  };

  const selectedLights = preset ? presets[preset] : lights || [];

  return (
    <>
      {selectedLights.map((light, index) => {
        if (!light.on) return null;

        if (light.type === "directional" && light.position) {
          return (
            <directionalLight
              key={index}
              position={[light.position.x, light.position.y, light.position.z]}
              intensity={light.intensity}
              color={light.color}
              castShadow={light.castShadow}
            />
          );
        }

        if (light.type === "ambient") {
          return <ambientLight key={index} intensity={light.intensity} color={light.color} />;
        }

        return null;
      })}
    </>
  );
};

export default GlobalLights;
