import * as THREE from "three";
import { AnimationData } from "@/interfaces/CoreInterfaces";

export function animationPlayer(poiObject: THREE.Object3D, animationData: AnimationData, play: boolean = true) {
  const animations = poiObject.animations as THREE.AnimationClip[];
  if (!animations || animations.length === 0) {
    console.warn(`No animations found for POI object.`);
    return;
  }

  const animation = animations.find((clip) => clip.name === animationData.name);
  if (!animation) {
    console.warn(`Animation ${animationData.name} not found`);
    return;
  }

  console.log(`${play ? "Playing" : "Pausing"} animation ${animationData.name}`);

  if (!poiObject.userData.mixer) {
    poiObject.userData.mixer = new THREE.AnimationMixer(poiObject);
  }

  const mixer = poiObject.userData.mixer as THREE.AnimationMixer;
  const action = mixer.clipAction(animation);

  action.setEffectiveTimeScale(animationData.speed ?? 1);
  action.loop = animationData.loop ? THREE.LoopRepeat : THREE.LoopOnce;
  action.clampWhenFinished = !animationData.resetWhenComplete;

  if (play) {
    if (animationData.restartOnPlay) {
      action.reset();
    }
    action.paused = false; // Ensure it's not paused
    action.timeScale = 1;
    action.play();
  } else {
    if (animationData.reverseOnFalse) {
      action.paused = false;
      action.timeScale = -1;
      action.play();
    } else {
      action.paused = true; // Pause instead of stopping
    }
  }

  const clock = poiObject.userData.clock || new THREE.Clock();
  poiObject.userData.clock = clock;

  const updateMixer = () => {
    const delta = clock.getDelta();
    if (mixer) mixer.update(delta);
    requestAnimationFrame(updateMixer);
  };

  updateMixer();
}
