import React, { useEffect, useState, useRef } from "react";
import { PoiFeature, VideoData } from "@/interfaces/CoreInterfaces";
import { scenePath } from "@/app/Routing";
import { useSpring } from "@react-spring/three";
import { useFrame } from "@react-three/fiber";
import * as THREE from "three";

interface VideoFrameProps {
  scene: THREE.Scene;
  camera: THREE.Camera;
  featureData: PoiFeature;
  videoData: VideoData;
  uiRef: React.RefObject<HTMLDivElement>;
  target: THREE.Object3D; // Target object to focus on
  onClose: () => void; // Callback to close the video
}

const VideoPlayer: React.FC<VideoFrameProps> = ({ scene, camera, featureData, videoData, uiRef, target, onClose }) => {
  const [animate, setAnimate] = useState(false);
  const cameraFocusObject = scene.getObjectByName("cameraFocusObject") as THREE.Object3D;

  const uiData = featureData.uiElement;

  // Refs to store original camera state
  const originalCameraPosition = useRef(camera.position.clone());
  const originalCameraFocus = useRef(cameraFocusObject.position.clone());

  // Spring animation configuration
  const [{ cameraPosition, cameraFocus }, api] = useSpring(() => ({
    cameraPosition: camera.position.toArray(),
    cameraFocus: cameraFocusObject.position.toArray(),
    config: { mass: 5, tension: 210, friction: 60, precision: 0.001, clamp: true },
    onStart: () => {
      setAnimate(true);
    },
    onRest: () => {
      setAnimate(false);
    },
  }));

  useEffect(() => {
    if (!videoData || !videoData.videoUrl || !uiRef.current) {
      console.error("Invalid videoData or uiRef!");
      return;
    }

    const videoUrl = `${scenePath}/videos/${videoData.videoUrl}`;

    // Create video container
    const videoContainer = document.createElement("div");
    videoContainer.className = "absolute top-0 left-0 w-full h-full bg-black flex justify-center items-center pointer-events-auto opacity-0";
    videoContainer.style.zIndex = "100";
    videoContainer.style.opacity = "0";

    // Create transition style
    const transitionStyle = videoData.fadeDuration ? `opacity ${videoData.fadeDuration / 1000}s ease-in-out` : "opacity 0.5s ease-in-out";

    videoContainer.style.transition = transitionStyle;
    // Create video element
    const videoElement = document.createElement("video");
    videoElement.src = videoUrl;
    videoElement.controls = true;
    videoElement.autoplay = true;
    videoElement.loop = videoData.loop ?? false; // Use loop setting
    videoElement.preload = "auto";
    videoElement.className = "max-w-full max-h-full";
    videoContainer.appendChild(videoElement);

    // Create close button
    const closeButton = document.createElement("button");
    closeButton.innerText = "X";
    closeButton.className = "absolute top-2 left-2 bg-gray-700 text-white text-lg rounded px-4 py-2 opacity-80 hover:opacity-100 pointer-events-auto";
    closeButton.style.zIndex = "101";
    closeButton.onclick = () => {
      if (videoData.fadeOut !== false) {
        reverseAnimation();
        videoContainer.style.opacity = "0";
        setTimeout(() => onClose(), videoData.fadeDuration ?? 500 + 10); // Wait for fade-out to complete, plus 10ms for good measure
      } else {
        onClose();
      }
    };
    videoContainer.appendChild(closeButton);

    // Handle video end event for exitAfterEnd functionality
    videoElement.onended = () => {
      if (videoData.exitAfterEnd !== false) {
        if (videoData.fadeOut !== false) {
          reverseAnimation();
          videoContainer.style.opacity = "0";
          setTimeout(() => onClose(), videoData.fadeDuration ?? 500 + 10); // Wait for fade-out to complete, plus 10ms for good measure
        } else {
          onClose();
        }
      }
    };

    // Append video container to the UI
    uiRef.current.appendChild(videoContainer);

    // Get the camera's current position
    const currentCameraPosition = camera.position.toArray() as THREE.Vector3Tuple;

    // Get OrbitControls' current focus point
    const currentFocusPoint = cameraFocusObject.position.toArray() as THREE.Vector3Tuple;

    // Find the POI's 3D position and rotation
    const poiRotation = new THREE.Euler();
    const poiPosition = new THREE.Vector3();
    if (target) {
      target.getWorldPosition(poiPosition);
      target.getWorldQuaternion(new THREE.Quaternion()).normalize();
      poiRotation.setFromQuaternion(target.quaternion);
    } else {
      console.warn("Target object not found.");
    }

    const startPositionOffset = new THREE.Vector3(uiData?.positionOffset?.x ?? 0, uiData?.positionOffset?.y ?? 0, uiData?.positionOffset?.z ?? 10);
    const targetPosition = poiPosition.clone().add(startPositionOffset.applyEuler(poiRotation));
    const targetFocus = targetPosition.clone().add(new THREE.Vector3(1, 0, 0));
    targetPosition.x -= 0.15; // Offset the camera's Y position

    videoElement.addEventListener("canplay", () => {
      // Start the spring animation
      api.start({
        from: { cameraPosition: currentCameraPosition, cameraFocus: currentFocusPoint },
        to: { cameraPosition: targetPosition.toArray() as THREE.Vector3Tuple, cameraFocus: targetFocus.toArray() },
      });

      // Apply fade-in effect
      if (videoData.fadeIn !== false) {
        setTimeout(() => {
          videoContainer.style.opacity = "1";
        }, 10); // Delay to allow the video to load
      } else {
        videoContainer.style.opacity = "1";
      }
    });

    // Function to reverse the animation to the original position
    const reverseAnimation = () => {
      api.start({
        from: {
          cameraPosition: camera.position.toArray() as THREE.Vector3Tuple,
          cameraFocus: cameraFocusObject.position.toArray() as THREE.Vector3Tuple,
        },
        to: {
          cameraPosition: originalCameraPosition.current.toArray() as THREE.Vector3Tuple,
          cameraFocus: originalCameraFocus.current.toArray() as THREE.Vector3Tuple,
        },
      });
    };

    // Cleanup function
    return () => {
      if (uiRef.current && uiRef.current.contains(videoContainer)) {
        uiRef.current.removeChild(videoContainer);
      }
    };
  }, [
    videoData,
    uiRef,
    camera,
    target,
    scene,
    api,
    cameraFocusObject.position,
    onClose,
    uiData?.positionOffset?.x,
    uiData?.positionOffset?.y,
    uiData?.positionOffset?.z,
  ]);

  useFrame(() => {
    if (animate) {
      camera.position.set(...cameraPosition.get());
      cameraFocusObject.position.set(...cameraFocus.get());
      camera.lookAt(cameraFocusObject.position);
    }
  });

  return null; // The component itself doesn't render anything
};

export default VideoPlayer;
