import React, { useEffect, useState } from "react";
import { useThree } from "@react-three/fiber";
import { usePoi } from "@/context/PoiContext";
import InteractionSwitch from "./InteractionSwitch";

const InteractionHandler: React.FC<{ uiRef: React.RefObject<HTMLDivElement> }> = ({ uiRef }) => {
  const { scene, camera, size } = useThree();
  const { activePoi, setActivePoi } = usePoi();
  const [isSceneReady, setIsSceneReady] = useState(false);

  useEffect(() => {
    let timeout: NodeJS.Timeout | null = null; // Initialize timeout with null
    const missingMeshIds: string[] = [];

    const checkSceneReady = () => {
      if (!activePoi) return false;

      const childPoisReady =
        activePoi.childPois?.every((childPoi) => {
          if (!childPoi.meshId) return false;

          const objectExists = !!scene.getObjectByName(childPoi.meshId);
          if (!objectExists && !missingMeshIds.includes(childPoi.meshId)) {
            missingMeshIds.push(childPoi.meshId);
          }
          return objectExists;
        }) ?? true;

      return childPoisReady;
    };

    const interval = setInterval(() => {
      if (checkSceneReady()) {
        setIsSceneReady(true);
        clearInterval(interval);
        if (timeout !== null) {
          clearTimeout(timeout);
        }
      }
    }, 100); // Check every 100ms

    timeout = setTimeout(() => {
      clearInterval(interval);
      setIsSceneReady(true); // Proceed even if some objects are missing
      if (missingMeshIds.length > 0) {
        console.error(`The following meshIds were not found in the scene and may cause issues: ${missingMeshIds.join(", ")}`);
      }
    }, 2000); // Maximum wait time of 5 seconds

    return () => {
      clearInterval(interval);
      clearTimeout(timeout);
    };
  }, [scene, activePoi]);

  if (!activePoi || !isSceneReady) return null;

  return (
    <>
      {/* Render child POIs */}
      {activePoi.childPois &&
        activePoi.childPois.map((childPoi) => {
          if (!childPoi.meshId) {
            console.warn(`POI ${childPoi.name} is missing meshId.`);
            return null;
          }

          if (!childPoi.uiElement) {
            console.warn(`POI ${childPoi.name} is missing iconSettings.`);
            return null;
          }

          const poiObject = scene.getObjectByName(childPoi.meshId);

          if (!poiObject) {
            console.warn(`Model with meshId ${childPoi.meshId} not found in the scene.`);
            return null;
          }

          return (
            <InteractionSwitch
              key={childPoi.name}
              scene={scene}
              camera={camera}
              uiRef={uiRef}
              size={size}
              target={poiObject}
              onInteract={() => setActivePoi(childPoi)}
              changePoi={true}
              childPoi={childPoi}
            />
          );
        })}

      {/* Render POI features */}
      {activePoi.poiFeatures &&
        activePoi.poiFeatures.map((poiFeature) => {
          const poiObject = activePoi.meshId ? scene.getObjectByName(activePoi.meshId) : null;

          if (!poiObject) {
            console.warn(`Model with meshId ${activePoi.meshId} not found in the scene.`);
            return null;
          }

          return (
            <InteractionSwitch
              key={poiFeature.name}
              uiRef={uiRef}
              scene={scene}
              camera={camera}
              size={size}
              target={poiObject}
              onInteract={() => console.log(`Interacting with feature: ${poiFeature.name}`)}
              changePoi={false}
              poiFeature={poiFeature}
            />
          );
        })}
    </>
  );
};

export default InteractionHandler;
