import React, { useEffect, useState } from "react";
import { usePoi } from "@/context/PoiContext";
import { PointOfInterest } from "../../interfaces/CoreInterfaces";
import BackButton from "@2dc/BackButton";

interface BackButtonProps {
  parentPoi: PointOfInterest;
}

const PoiBackButton: React.FC<BackButtonProps> = ({ parentPoi }) => {
  const { activePoi, setActivePoi, previousPoi } = usePoi();
  const [showBackButton, setShowBackButton] = useState(false);
  const [showHomeButton, setShowHomeButton] = useState(false);

  useEffect(() => {
    setShowBackButton(activePoi !== parentPoi);
    setShowHomeButton(activePoi !== parentPoi && previousPoi !== parentPoi);
  }, [activePoi, parentPoi, previousPoi]);

  if (showBackButton) {
    if (!showHomeButton) return <BackButton onClick={() => setActivePoi(previousPoi)}>Back</BackButton>;
    else {
      return (
        <>
          <BackButton onClick={() => setActivePoi(previousPoi)}>Back</BackButton>
          <BackButton onClick={() => setActivePoi(parentPoi)}>Home</BackButton>
        </>
      );
    }
  }

  return null;
};

export default PoiBackButton;
