import React, { useRef, useState, useEffect } from "react";
import * as THREE from "three";
import { useFrame } from "@react-three/fiber";
import { ModelData } from "../../../interfaces/CoreInterfaces";
import Model from "@/utils/AddModel";

const uprightQuaternion = new THREE.Quaternion(); // Quaternion for upright position
const yAxis = new THREE.Vector3(0, 1, 0);
const xAxis = new THREE.Vector3(1, 0, 0);
const quaternionX = new THREE.Quaternion();
const quaternionY = new THREE.Quaternion();
const combined = new THREE.Quaternion();

const RotatingModel: React.FC<{
  modelData: ModelData;
  canvasRef: React.RefObject<HTMLCanvasElement>;
  transitioning: boolean;
  direction: number;
  xView: boolean;
  onClick?: () => void;
}> = ({ modelData, canvasRef, transitioning, direction, xView, onClick }) => {
  const idleRef = useRef<THREE.Group>(null); // Parent group for idle rotation
  const modelRef = useRef<THREE.Group>(null); // Child group for user interaction
  const targetQuaternion = useRef(new THREE.Quaternion()); // Target rotation for user interaction
  const groupRef = useRef<THREE.Group>(null); // Group for idle movement

  const [isDragging, setIsDragging] = useState(false);
  const [isMoving, setIsMoving] = useState(false); // Track if the group is moving
  const [switched, setSwitched] = useState(false); // Track if the group has switched direction

  // Parameters for user rotation
  const rotationDamping = 0.08; // Smoothing factor for movement
  const returnSpeed = 0.015; // Speed of returning to the upright position

  // Parameters for idle rotation
  const idleTilt = useRef(0); // Track tilt angle
  const idleSpeed = 0.002; // Speed of idle rotation
  const tiltSpeed = 0.002; // Speed of tilt oscillation
  const tiltRange = 0.3; // Maximum tilt angle (in radians)

  useEffect(() => {
    if (!canvasRef.current) return;

    const handlePointerDown = () => setIsDragging(true);
    const handlePointerUp = () => setIsDragging(false);

    const handlePointerMove = (event: MouseEvent) => {
      if (isDragging && modelRef.current) {
        // Update target rotation in world space
        const deltaX = event.movementX * 0.004;
        const deltaY = event.movementY * 0.004;

        quaternionX.setFromAxisAngle(yAxis, deltaX); // Rotate around Y-axis
        quaternionY.setFromAxisAngle(xAxis, deltaY); // Rotate around X-axis
        combined.multiplyQuaternions(quaternionX, quaternionY);

        // Update target quaternion
        targetQuaternion.current.multiplyQuaternions(combined, targetQuaternion.current);
      }
    };

    // Add listeners to the window to capture events even if mouse moves outside the canvas
    window.addEventListener("pointermove", handlePointerMove);
    canvasRef.current.addEventListener("pointerdown", handlePointerDown);
    window.addEventListener("pointerup", handlePointerUp);

    // Clean up event listeners
    return () => {
      window.removeEventListener("pointermove", handlePointerMove);
      window.removeEventListener("pointerup", handlePointerUp);
    };
  }, [isDragging, canvasRef]);

  useFrame(() => {
    if (modelRef.current) {
      // Interpolate rotation towards the target rotation
      modelRef.current.quaternion.slerp(targetQuaternion.current, rotationDamping);

      // When not dragging, return to upright position, but ignore sideways rotation
      if (!isDragging) {
        targetQuaternion.current.slerp(uprightQuaternion, returnSpeed);
      }
    }

    if (idleRef.current && !isDragging) {
      // Apply idle rotation and tilt
      idleRef.current.rotation.y += idleSpeed;
      if (isMoving) {
        idleRef.current.rotation.y += 0.1;
      }

      // Update tilt angle using a sine wave for oscillation
      idleTilt.current += tiltSpeed;
      idleRef.current.rotation.z = Math.sin(idleTilt.current) * tiltRange;
    }

    if (groupRef.current) {
      if (isMoving || transitioning) {
        // Set isMoving to true
        setIsMoving(true);
        if (transitioning) {
          setSwitched(false);
          groupRef.current.position.x += 0.002 * direction;
        } else {
          if (!switched) {
            // Set the group x position * -1 to switch direction
            groupRef.current.position.x *= -1;
            setSwitched(true);
          }
          groupRef.current.position.x += 0.002 * direction;
          // IF the group is close to 0, set isMoving to false
          if (Math.abs(groupRef.current.position.x) < 0.005) {
            groupRef.current.position.x = 0;
            setIsMoving(false);
          }
        }
      }
    }
  });

  return (
    <group ref={groupRef} onClick={onClick}>
      <group ref={idleRef}>
        <group ref={modelRef}>
          <Model modelData={modelData} xView={xView} />
        </group>
      </group>
    </group>
  );
};

export default RotatingModel;
