import React, { useRef, useEffect } from "react";
import * as THREE from "three";
import { useThree, useFrame } from "@react-three/fiber";
import { useSpring } from "@react-spring/three";

interface CameraPanAndZoomProps {
  positionOffset: [x: number, y: number, z: number];
  initialTarget: [x: number, y: number, z: number];
  panLimits: { left: number; right: number; up: number; down: number };
  zoomLimits: { min: number; max: number };
  initialZoom?: number;
  zoomSpeed?: number;
  panSpeed?: number;
}

const CameraPanAndZoom: React.FC<CameraPanAndZoomProps> = ({
  positionOffset,
  initialTarget,
  panLimits,
  zoomLimits,
  initialZoom = 10,
  zoomSpeed = 0.1,
  panSpeed = 0.1,
}) => {
  const { camera, gl, scene } = useThree();
  const [animate, setAnimate] = React.useState(false);

  const lastMousePosition = useRef(new THREE.Vector2());
  const lastTouchDistance = useRef(0);

  const isDragging = useRef(false);
  const hasDragged = useRef(false);
  const targetRef = useRef(initialTarget);
  const zoomRef = useRef(initialZoom);

  const [{ cameraTarget, zoomTarget }, api] = useSpring(() => ({
    cameraTarget: targetRef.current,
    zoomTarget: zoomRef.current,
    config: { mass: 1, tension: 210, friction: 30, precision: 0.001, clamp: true },
    onStart: () => setAnimate(true),
    onRest: () => setAnimate(false),
  }));

  const cameraFocusObject: THREE.Object3D | null = scene.getObjectByName("cameraFocusObject") as THREE.Object3D;
  if (!cameraFocusObject) {
    console.error("Camera focus object not found in the scene.");
  }

  // Camera movement functions

  const CameraZoom = (deltaZoom: number) => {
    zoomRef.current = THREE.MathUtils.clamp(zoomRef.current + deltaZoom, zoomLimits.min, zoomLimits.max);
    api.start({ zoomTarget: zoomRef.current });
  };
  const CameraMove = (deltaX: number, deltaY: number) => {
    const newTarget = new THREE.Vector3(cameraTarget.get()[0] + deltaX, cameraTarget.get()[1] + deltaY, cameraTarget.get()[2]);

    // Constrain to pan limits
    newTarget.x = THREE.MathUtils.clamp(newTarget.x, initialTarget[0] - panLimits.left, initialTarget[0] + panLimits.right);
    newTarget.y = THREE.MathUtils.clamp(newTarget.y, initialTarget[1] - panLimits.down, initialTarget[1] + panLimits.up);

    targetRef.current = newTarget.toArray();
    api.start({ cameraTarget: targetRef.current });
  };

  // Input event handlers

  // ********************
  // *** Mouse events ***
  // ********************

  const handlePointerDown = (event: MouseEvent | TouchEvent) => {
    isDragging.current = true;
    hasDragged.current = false; // Reset drag detection

    if (event instanceof MouseEvent) {
      lastMousePosition.current.set(event.clientX, event.clientY);
    } else {
      event.preventDefault(); // Prevent scrolling
      const touch = event.touches[0];
      lastMousePosition.current.set(touch.clientX, touch.clientY);
    }
  };

  const handlePointerUp = (event: MouseEvent | TouchEvent) => {
    isDragging.current = false;

    if (hasDragged.current) {
      // Prevent click events only if it was a drag
      event.preventDefault();
      event.stopPropagation();
    }

    // Reset drag and pinch states
    hasDragged.current = false;
    lastTouchDistance.current = 0;
  };

  const handlePointerMove = (event: MouseEvent | TouchEvent) => {
    if (!isDragging.current) return;

    const currentMousePosition = new THREE.Vector2();
    if (event instanceof MouseEvent) {
      currentMousePosition.set(event.clientX, event.clientY);
    } else {
      event.preventDefault(); // Prevent scrolling
      const touch = event.touches[0];
      currentMousePosition.set(touch.clientX, touch.clientY);
    }

    const delta = currentMousePosition.clone().sub(lastMousePosition.current);
    lastMousePosition.current.copy(currentMousePosition);

    if (delta.length() > 2) {
      hasDragged.current = true; // Mark as drag
    }

    const aspect = gl.domElement.clientWidth / gl.domElement.clientHeight;

    // Calculate pan offsets
    const panX = delta.x * panSpeed * aspect;
    const panY = delta.y * panSpeed;

    CameraMove(-panX, panY);
  };

  // ********************
  // *** Touch events ***
  // ********************

  const handleTouchMove = (event: TouchEvent) => {
    event.preventDefault();

    if (event.touches.length === 2) {
      // Pinch zoom logic
      const touch1 = event.touches[0];
      const touch2 = event.touches[1];

      const currentDistance = Math.hypot(touch1.clientX - touch2.clientX, touch1.clientY - touch2.clientY);

      const deltaDistance = currentDistance - lastTouchDistance.current;
      lastTouchDistance.current = currentDistance;

      if (!hasDragged.current) {
        hasDragged.current = true;
        return;
      }

      CameraZoom(-deltaDistance * zoomSpeed * 2);
    } else if (event.touches.length === 1) {
      // Pan logic
      const touch = event.touches[0];

      const currentMousePosition = new THREE.Vector2(touch.clientX, touch.clientY);

      const delta = currentMousePosition.clone().sub(lastMousePosition.current);
      lastMousePosition.current.copy(currentMousePosition);
      if (!hasDragged.current) {
        hasDragged.current = true;
        return;
      }

      const panX = delta.x * panSpeed;
      const panY = delta.y * panSpeed;

      CameraMove(-panX, panY);
    }
  };

  const handleTouchEnd = () => {
    hasDragged.current = false;
  };

  const handleWheel = (event: WheelEvent) => {
    if (event.target instanceof HTMLElement && event.target.closest("#info-window-container")) {
      return;
    }
    event.preventDefault();

    CameraZoom(event.deltaY * zoomSpeed);
  };

  // Event listeners & cleanup

  useEffect(() => {
    const options = { passive: false }; // Allow preventDefault for touch events

    window.addEventListener("mousedown", handlePointerDown);
    window.addEventListener("mousemove", handlePointerMove);
    window.addEventListener("mouseup", handlePointerUp);

    window.addEventListener("touchmove", handleTouchMove, options);
    window.addEventListener("touchend", handleTouchEnd);

    window.addEventListener("wheel", handleWheel, options);

    return () => {
      window.removeEventListener("mousedown", handlePointerDown);
      window.removeEventListener("mousemove", handlePointerMove);
      window.removeEventListener("mouseup", handlePointerUp);

      window.removeEventListener("touchmove", handleTouchMove);
      window.addEventListener("touchend", handleTouchEnd);

      window.removeEventListener("wheel", handleWheel);
    };
  });

  // Animation loop

  useFrame(() => {
    if (!animate) return;

    // Set the position of the camera to the cameraTarget offset by the positionOffset
    camera.position.set(cameraTarget.get()[0] + positionOffset[0], cameraTarget.get()[1] + positionOffset[1], zoomTarget.get());
    // Update the position of the camera focus object
    cameraFocusObject.position.set(...cameraTarget.get());
    camera.lookAt(cameraFocusObject.position);
  });

  return null;
};

export default CameraPanAndZoom;
