import React, { useEffect, useRef, useState } from "react";
import * as THREE from "three";
import { useFrame, useThree } from "@react-three/fiber";
import { MapControls, OrbitControls } from "@react-three/drei";
import { usePoi } from "@/context/PoiContext";
import { useSpring } from "@react-spring/three";
import { DebugSettings } from "@/interfaces/CoreInterfaces";
import CameraPanAndZoom from "../../utils/camera/CameraPanAndZoom";

const CameraController: React.FC<{ debugSettings?: DebugSettings }> = ({ debugSettings }) => {
  const { camera, scene } = useThree();
  const { activePoi } = usePoi();

  // @ts-expect-error: orbitRef can and must be a ref to OrbitControls even though it is not a type
  const orbitRef = useRef<OrbitControls | null>(null);
  const poiObject = useRef<THREE.Object3D | null>(null);
  const controlSettings = useRef(activePoi?.cameraControls || null);
  const lastPoiPosition = useRef(new THREE.Vector3());

  const [animate, setAnimate] = useState(false);
  const [follow, setFollow] = useState(true);

  const debug = debugSettings?.allTrue || false;

  // Create a reference to the camera focus target object
  let cameraFocusObject: THREE.Object3D | null = scene.getObjectByName("cameraFocusObject") as THREE.Object3D;
  if (!cameraFocusObject) {
    cameraFocusObject = new THREE.Object3D();
    cameraFocusObject.name = "cameraFocusObject";

    if (debug) {
      const geometry = new THREE.SphereGeometry(0.05, 32, 32);
      const material = new THREE.MeshBasicMaterial({ color: 0xff0000 });
      const debugSphere = new THREE.Mesh(geometry, material);
      cameraFocusObject.add(debugSphere); // Add debug sphere to the focus object
    }

    scene.add(cameraFocusObject);
  }

  // Spring animation configuration
  const [{ cameraPosition, cameraFocus }, api] = useSpring(() => ({
    cameraPosition: camera.position.toArray(),
    cameraFocus: cameraFocusObject.position.toArray(),
    config: { mass: 5, tension: 210, friction: 60, precision: 0.001, clamp: true },
    onStart: () => {
      setAnimate(true);
    },
    onRest: () => {
      setAnimate(false);
    },
  }));

  useEffect(() => {
    if (!activePoi || !activePoi.cameraControls) {
      console.warn("Active POI or camera controls are not defined.");
      return;
    }

    controlSettings.current = { ...activePoi.cameraControls };

    // Get the camera's current position
    const currentCameraPosition = camera.position.toArray() as THREE.Vector3Tuple;

    // Get OrbitControls' current focus point
    const currentFocusPoint = cameraFocusObject.position.toArray() as THREE.Vector3Tuple;

    // Find the POI's 3D position and rotation
    const poiRotation = new THREE.Euler();
    const poiPosition = new THREE.Vector3();
    if (activePoi.meshId) {
      poiObject.current = scene.getObjectByName(activePoi.meshId) || null;
      if (poiObject !== null) {
        poiObject.current?.getWorldPosition(poiPosition);
        poiObject.current?.getWorldQuaternion(new THREE.Quaternion()).normalize();
        poiRotation.setFromQuaternion(poiObject.current?.quaternion ?? new THREE.Quaternion());
      } else {
        console.warn(`3D object with meshId ${activePoi.meshId} not found.`);
      }
    }

    // Calculate the target position and focus
    const startPositionOffset = new THREE.Vector3(
      controlSettings.current.startPosition?.x ?? 0,
      controlSettings.current.startPosition?.y ?? 0,
      controlSettings.current.startPosition?.z ?? 10
    );

    const rotatedOffset = startPositionOffset.clone().applyEuler(poiRotation);
    const targetPosition = poiPosition.clone().add(rotatedOffset);
    const targetFocus = poiPosition.clone();
    if (controlSettings.current.targetOffset) {
      // add the target offset to the focus point
      targetFocus.add(
        new THREE.Vector3(controlSettings.current.targetOffset.x, controlSettings.current.targetOffset.y, controlSettings.current.targetOffset.z)
      );
    }

    if (activePoi.cameraControls.follow) {
      setFollow(true);
    }

    // Start the spring animation
    api.start({
      from: { cameraPosition: currentCameraPosition, cameraFocus: currentFocusPoint },
      to: { cameraPosition: targetPosition.toArray() as THREE.Vector3Tuple, cameraFocus: targetFocus.toArray() },
    });

    console.log("Active POI:\n", activePoi.name, "\nMesh id: ", activePoi.meshId, "\nMoving camera to:\n", targetPosition, "\nLooking at:\n", targetFocus);

    // Cleanup by setting follow to false
    return () => {
      setFollow(false);
    };
  }, [activePoi]);

  // Update the camera position and look-at in the frame loop
  useFrame(() => {
    if (animate || follow) {
      // update the position of the camera focus target object
      cameraFocusObject.position.set(...cameraFocus.get());
    }
    if (animate) {
      // if (activePoi.cameraControls?.type !== undefined && activePoi.cameraControls.type !== "orbit") {
      camera.position.set(...cameraPosition.get());
      camera.lookAt(cameraFocusObject.position);
      /* } else {
        if (orbitRef.current) {
          orbitRef.current.object.position.set(...cameraPosition.get());
          orbitRef.current.target.set(cameraFocusObject.position.x, cameraFocusObject.position.y, cameraFocusObject.position.z);
        } 
      } */
    }
    if (follow) {
      if (poiObject.current) {
        // Find out how much the poi object has moved since the last frame
        const poiMovement = poiObject.current.position.clone().sub(lastPoiPosition.current);
        // move the camera the same amount
        camera.position.add(poiMovement);
        // Update the last position
        lastPoiPosition.current = poiObject.current.position.clone();
        cameraFocusObject.position.set(poiObject.current.position.x, poiObject.current.position.y, poiObject.current.position.z);
        if (orbitRef.current) {
          // Set the camera target to the POI's position
          if (orbitRef.current.target) {
            (orbitRef.current.target as THREE.Vector3).copy(cameraFocusObject.position);
          }
        } else {
          camera.lookAt(cameraFocusObject.position);
        }
      }
    }
  });

  if (animate) {
    return null;
  }

  if (activePoi.cameraControls?.type === "map") {
    return (
      <MapControls
        enableDamping={activePoi?.cameraControls?.enableDamping ?? true}
        dampingFactor={activePoi?.cameraControls?.dampingFactor ?? 0.05}
        enableZoom={activePoi?.cameraControls?.enableZoom ?? true}
        enableRotate={activePoi?.cameraControls?.enableRotate ?? true}
        enablePan={activePoi?.cameraControls?.enablePan ?? true}
        screenSpacePanning={activePoi?.cameraControls?.screenSpacePanning ?? true}
        target={cameraFocusObject.position}
        maxPolarAngle={
          activePoi?.cameraControls?.constraints?.up !== undefined ? THREE.MathUtils.degToRad(90 - activePoi.cameraControls.constraints.up) : Math.PI
        }
        minPolarAngle={
          activePoi?.cameraControls?.constraints?.down !== undefined ? THREE.MathUtils.degToRad(90 + activePoi.cameraControls.constraints.down) : 0
        }
        minAzimuthAngle={
          activePoi?.cameraControls?.constraints?.left !== undefined ? THREE.MathUtils.degToRad(-activePoi.cameraControls.constraints.left) : -Infinity
        }
        maxAzimuthAngle={
          activePoi?.cameraControls?.constraints?.right !== undefined ? THREE.MathUtils.degToRad(activePoi.cameraControls.constraints.right) : Infinity
        }
      />
    );
  } else if (activePoi.cameraControls?.type === "elevator") {
    return (
      <CameraPanAndZoom
        positionOffset={[0, 0, 10]}
        initialTarget={
          activePoi.cameraControls.targetOffset
            ? [activePoi.cameraControls.targetOffset.x, activePoi.cameraControls.targetOffset.y, activePoi.cameraControls.targetOffset.z]
            : [0, 0, 0]
        }
        panLimits={{ left: 3.5, right: 3.5, up: 8, down: 4.5 }}
        zoomLimits={{ min: 4, max: 16 }}
        zoomSpeed={0.01}
        panSpeed={0.1}
      />
    );
  } else if (activePoi.cameraControls?.type === "orbit") {
    return (
      <OrbitControls
        ref={orbitRef}
        args={[camera]}
        target={cameraFocusObject.position}
        enablePan={activePoi?.cameraControls?.enablePan ?? false}
        enableZoom={activePoi?.cameraControls?.enableZoom ?? true}
        enableRotate={activePoi?.cameraControls?.enableRotate ?? true}
        rotateSpeed={activePoi?.cameraControls?.rotateSpeed ?? 1}
        enableDamping={activePoi?.cameraControls?.enableDamping ?? true}
        dampingFactor={activePoi?.cameraControls?.dampingFactor ?? 0.05}
        maxDistance={activePoi?.cameraControls?.constraints?.maxZoom ?? Infinity}
        minDistance={activePoi?.cameraControls?.constraints?.minZoom ?? 0}
        minPolarAngle={activePoi?.cameraControls?.constraints?.up !== undefined ? THREE.MathUtils.degToRad(90 - activePoi.cameraControls.constraints.up) : 0}
        maxPolarAngle={
          activePoi?.cameraControls?.constraints?.down !== undefined ? THREE.MathUtils.degToRad(90 + activePoi.cameraControls.constraints.down) : Math.PI
        }
        minAzimuthAngle={
          activePoi?.cameraControls?.constraints?.left !== undefined ? THREE.MathUtils.degToRad(-activePoi.cameraControls.constraints.left) : -Infinity
        }
        maxAzimuthAngle={
          activePoi?.cameraControls?.constraints?.right !== undefined ? THREE.MathUtils.degToRad(activePoi.cameraControls.constraints.right) : Infinity
        }
      />
    );
  } else {
    return null;
  }
};

export default CameraController;
