import * as THREE from "three";
import { UiElement } from "@/interfaces/CoreInterfaces";

export default function ClickableObject(camera: THREE.Camera, uiElement: UiElement, target: THREE.Object3D, onClick: () => void): () => void {
  const raycaster = new THREE.Raycaster();
  const mouse = new THREE.Vector2();

  let isHovered = false;

  const hlSettings = uiElement.highlightSettings;

  const highlightIntensity = hlSettings?.intensity ? hlSettings.intensity : 0.4;
  const highlightColor = hlSettings?.color ? hlSettings.color : 0xffffbb;

  // Map to store original materials and their clones
  const originalMaterials = new Map<THREE.Mesh, THREE.Material>();

  const createMaterialCopy = (mesh: THREE.Mesh) => {
    if (!originalMaterials.has(mesh)) {
      if (Array.isArray(mesh.material)) {
        originalMaterials.set(mesh, mesh.material[0]);
        const newMaterial = mesh.material[0].clone();
        mesh.material = [newMaterial];
      } else {
        originalMaterials.set(mesh, mesh.material);
        const newMaterial = mesh.material.clone();
        mesh.material = newMaterial;
      }
    }
  };

  const restoreOriginalMaterial = (mesh: THREE.Mesh) => {
    if (originalMaterials.has(mesh)) {
      mesh.material = originalMaterials.get(mesh) as THREE.Material;
      originalMaterials.delete(mesh);
    }
  };

  const onMouseMove = (event: MouseEvent) => {
    mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
    mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

    raycaster.setFromCamera(mouse, camera);
    const intersects = raycaster.intersectObject(target, true); // Include children

    if (intersects.length > 0) {
      if (!isHovered) {
        isHovered = true;
        // Update cursor
        document.body.style.cursor = "pointer";
        console.log("Hovered over object");

        target.traverse((child) => {
          if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
            createMaterialCopy(child);
            (child.material as THREE.MeshStandardMaterial).emissiveIntensity = highlightIntensity;
            (child.material as THREE.MeshStandardMaterial).emissive.set(highlightColor);
          }
        });
      }
    } else if (isHovered) {
      isHovered = false;
      document.body.style.cursor = "auto";

      // Remove the target and its children from the outline layer
      target.traverse((child) => {
        if (child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial) {
          (child.material as THREE.MeshStandardMaterial).emissiveIntensity = 0;
          (child.material as THREE.MeshStandardMaterial).emissive.set(0x000000);
        }
      });
    }
  };

  const onMouseClick = () => {
    if (isHovered) {
      onClick();
    }
  };

  document.addEventListener("mousemove", onMouseMove);
  document.addEventListener("click", onMouseClick);

  return () => {
    document.removeEventListener("mousemove", onMouseMove);
    document.removeEventListener("click", onMouseClick);
    document.body.style.cursor = "auto";

    // Cleanup: Restore original materials
    target.traverse((child) => {
      if (child instanceof THREE.Mesh) {
        restoreOriginalMaterial(child);
      }
    });
  };
}
