import React, { useEffect, useState } from "react";
import * as THREE from "three";
import { useThree } from "@react-three/fiber";
import { AnimationData, PoiFeature, PointOfInterest, UiElement, VideoData } from "@/interfaces/CoreInterfaces";
import { animationPlayer } from "@/functions/AnimationPlayer";
import createButton from "@/utils/ui/CreateButton";
import createToggle from "@/utils/ui/CreateToggle";
import VideoPlayer from "@/functions/VideoPlayer";
import { createPoiIcon } from "@3dc/CreateIcon";
import ClickableObject from "@3dc/ClickableObject";

interface InteractionSwitchProps {
  uiRef: React.RefObject<HTMLDivElement>;
  scene: THREE.Scene;
  camera: THREE.Camera;
  size: { width: number; height: number };
  target: THREE.Object3D | null;
  onInteract: () => void;
  changePoi?: boolean;
  poiFeature?: PoiFeature;
  childPoi?: PointOfInterest;
}

const InteractionSwitch: React.FC<InteractionSwitchProps> = ({ uiRef, scene, camera, size, target, onInteract, changePoi, poiFeature, childPoi }) => {
  const { gl: renderer } = useThree();
  const [toggleState, setToggleState] = useState(true);
  const [showVideo, setShowVideo] = useState(false);

  useEffect(() => {
    const cleanupFunctions: (() => void)[] = [];

    if (!uiRef.current || !target) {
      console.error("Invalid UI ref or target");
      return;
    }
    if (!poiFeature && !childPoi) {
      console.error("No POI feature or child POI provided.");
      return;
    }

    let uiElement: UiElement;
    let featureData: AnimationData | VideoData | undefined;

    if (poiFeature) {
      uiElement = poiFeature.uiElement || ({} as UiElement);
      featureData = poiFeature.data as AnimationData;
    } else {
      uiElement = childPoi?.uiElement || ({} as UiElement);
    }

    const handleToggle = () => {
      setToggleState(!toggleState);
      switch (poiFeature?.type) {
        case "videoFrame":
          setShowVideo(!showVideo);
          break;
        case "animation":
          animationPlayer(target, featureData as AnimationData, toggleState);
          break;
        default:
          console.warn("No valid type found for toggle.");
          break;
      }
    };

    const handleButtonClick = () => {
      if (poiFeature?.type === "videoFrame") {
        setShowVideo(true); // Show the video
      } else if (poiFeature?.type === "animation" && featureData) {
        animationPlayer(target, featureData as AnimationData, true);
      }
    };

    let cleanup: (() => void) | undefined;

    switch (uiElement.type) {
      case "icon":
        if (target) {
          if (changePoi) {
            cleanup = createPoiIcon(uiElement, uiRef, camera, size, target, onInteract) || undefined;
          } else {
            cleanup = createPoiIcon(uiElement, uiRef, camera, size, target, handleButtonClick) || undefined;
          }
        }
        break;

      case "button":
        if (changePoi) {
          cleanup = createButton(uiElement, uiRef, onInteract);
        } else {
          cleanup = createButton(uiElement, uiRef, handleButtonClick);
        }
        break;

      case "toggle":
        if (changePoi) {
          cleanup = createToggle(uiElement, uiRef, onInteract, toggleState);
        } else {
          cleanup = createToggle(uiElement, uiRef, handleToggle, toggleState);
        }
        break;

      case "clickable":
        if (target) {
          if (changePoi) {
            cleanup = ClickableObject(camera, uiElement, target, onInteract);
          } else {
            ClickableObject(camera, uiElement, target, handleButtonClick);
          }
        } else {
          console.warn("No valid target found for highlight.");
        }
        break;

      default:
        if (target) {
          if (changePoi) {
            cleanup = createPoiIcon(uiElement, uiRef, camera, size, target, onInteract) || undefined;
          } else {
            cleanup = createPoiIcon(uiElement, uiRef, camera, size, target, handleButtonClick) || undefined;
          }
        } else {
          if (changePoi) {
            cleanup = createButton(uiElement, uiRef, onInteract);
          } else {
            cleanup = createButton(uiElement, uiRef, onInteract);
          }
        }
        break;
    }

    if (cleanup) {
      cleanupFunctions.push(cleanup);
    }

    return () => {
      cleanupFunctions.forEach((cleanup) => cleanup());
    };
  }, [uiRef, camera, size, target, onInteract, poiFeature, childPoi, toggleState, changePoi, showVideo, renderer, scene]);

  return (
    <>
      {showVideo && poiFeature?.type === "videoFrame" && (
        <VideoPlayer
          scene={scene}
          camera={camera}
          featureData={poiFeature}
          videoData={poiFeature.data as VideoData}
          uiRef={uiRef}
          target={target!}
          onClose={() => setShowVideo(false)} // Hide the video on close
        />
      )}
    </>
  );
};

export default InteractionSwitch;
