import * as THREE from "three";
import React, { useEffect, useRef } from "react";
import { useGLTF } from "@react-three/drei";
import { ModelData } from "@/interfaces/CoreInterfaces";
import { scenePath } from "@/app/Routing";

const XViewModel: React.FC<{ modelData: ModelData; scale: number; xView: boolean; tag?: string }> = ({ modelData, scale, xView, tag }) => {
  const modelPath = scenePath + modelData.path;
  const { nodes, materials } = useGLTF(modelPath);

  if (!nodes || !materials) {
    console.error("Model nodes or materials are undefined");
  }

  const xViewSettings = modelData.xView;
  if (!xViewSettings) {
    console.error("Model XView settings are undefined. Update project data to include XView settings, or remove the xView prop from the model loader.");
  }

  if (tag) {
    Object.values(nodes).forEach((node) => {
      node.userData.tag = tag;
    });
  }

  // Track refs for fade meshes
  const fadeMeshRefs = useRef<Record<string, THREE.Mesh>>({});

  // Handle fade animation for fadeMesh
  useEffect(() => {
    const fadeMeshes = xViewSettings.fadeMesh;
    const fadeAmount = xViewSettings.fadeAmount !== undefined ? 1 - xViewSettings.fadeAmount : 0; // Default opacity is 0

    fadeMeshes.forEach(({ nodeId }) => {
      const mesh = fadeMeshRefs.current[nodeId];
      if (!mesh) return;

      const originalMaterial = Array.isArray(mesh.material) ? materials[mesh.material[0].name] : materials[mesh.material.name]; // Find the original material by name
      const clonedMaterial = originalMaterial.clone(); // Clone the material to avoid affecting other meshes
      clonedMaterial.transparent = true; // Enable transparency
      clonedMaterial.opacity = 1; // Start fully opaque

      mesh.material = clonedMaterial;

      const targetOpacity = xView ? fadeAmount : 1; // Fade out when xView is true, fade in when false
      const fadeSpeed = 0.05; // Adjust for smoother fading

      const fade = () => {
        if (!clonedMaterial) return;
        clonedMaterial.opacity = THREE.MathUtils.lerp(clonedMaterial.opacity, targetOpacity, fadeSpeed);

        if (clonedMaterial.opacity <= 0.01) {
          mesh.visible = false; // Hide mesh completely when opacity is near 0
        } else {
          mesh.visible = true; // Ensure visibility otherwise
          requestAnimationFrame(fade);
        }
      };

      fade();
    });
  }, [xView, xViewSettings.fadeMesh, xViewSettings.fadeAmount, materials]);

  const renderStandardMeshes = () => {
    if (!xView) {
      return xViewSettings.standardMesh?.map(({ nodeId, materialId }) => {
        const meshNode = nodes[nodeId] as THREE.Mesh;
        const material = materials[materialId];
        if (!meshNode || !material) {
          console.warn(`Node or material not found for standard mesh: ${nodeId}`);
          return null;
        }
        return <mesh key={nodeId} geometry={meshNode.geometry} material={material} />;
      });
    }
    return null; // Do not render standardMesh when xView is true
  };

  const renderXViewMeshes = () => {
    if (xView) {
      return xViewSettings.xViewMesh?.map(({ nodeId, materialId }) => {
        const meshNode = nodes[nodeId] as THREE.Mesh;
        const material = materials[materialId];
        if (!meshNode || !material) {
          console.warn(`Node or material not found for xView mesh: ${nodeId}`);
          return null;
        }
        return <mesh key={nodeId} geometry={meshNode.geometry} material={material} />;
      });
    }
    return null; // Do not render xViewMesh when xView is false
  };

  const renderFadeMeshes = () => {
    if (xView) {
      return xViewSettings.fadeMesh.map(({ nodeId, materialId }) => {
        const meshNode = nodes[nodeId] as THREE.Mesh;
        const material = materials[materialId];
        if (!meshNode || !material) {
          console.warn(`Node or material not found for fade mesh: ${nodeId}`);
          return null;
        }
        return (
          <mesh
            key={nodeId}
            ref={(ref) => {
              if (ref) fadeMeshRefs.current[nodeId] = ref;
            }}
            geometry={meshNode.geometry}
            material={material}
          />
        );
      });
    }
    return null; // Do not render fadeMesh when xView is false
  };

  return (
    <group scale={[scale, scale, scale]}>
      {renderStandardMeshes()}
      {renderXViewMeshes()}
      {renderFadeMeshes()}
    </group>
  );
};

export default XViewModel;
